//@import "../../plugins/Hplus-v.4.1.0/css/style.css";

@import "../mixins/mixins-color";
@import "../components/m-avatar";


/*page-heading*/
.page-heading{
  h2,
  .p-page-header_form{
    display: inline-block;
  }
  .p-page-header_form{
    vertical-align: middle;
    margin-top:-10px;
    margin-left:10px;
  }
}
.gohome{
  top:70px;
}
.message-avatar > img {
  display: block;
  width: 100%;
  height: 100%;
}
.chat-message--left {
  .message{
    margin-left:55px;
  }

  .message-avatar{
    margin-right:10px;
    float:left;
  }
  .message-date{
    float:right;
  }
}
.chat-message--condensed{
  padding:5px 0;
}
//
.m-msg-list{
  .m-msg{
    padding:10px 0;
    border-bottom:1px solid $color-ibox-border;
    &:first-child{
      padding-top:0;
    }
    &:last-child{
      border-bottom:none;
    }
  }
}
.m-msg{
  .m-avatar{
    margin-right:10px;
  }
  p{
    margin-bottom:0;
  }
}

.editable-popup{
  .popover-title{
    margin-top:0;
  }
}
/*bootstrap-table*/
.fixed-table-body.overflow--v{
  overflow-x: visible;
  overflow-y: visible;
}
/*popover*/
.popover{
  .popover-title{
    margin-top:0;
  }
}
/*ibox-----------------------------------------------------------*/
.ibox-footer{
  padding:15px 20px 20px;
  margin-top: -5px;
}
.stat-title{
  display:inline-block;
  margin-top:5px;
}
.checkbox input[type=checkbox],
.checkbox-inline input[type=checkbox],
.radio input[type=radio],
.radio-inline input[type=radio]{
  margin-top:4px;
}
.ibox-body{
  position: relative;
}
/*btn------------------------------------------------------------*/
.btn .badge{
  padding-bottom:2px;
}