@import "helpers/h-style";
@import "base/global";
@import "components/style";
@import "components/m-charts";
@import "components/m-ibox";
@import "components/m-btn";

@import "mixins/mixins-color";



/*------------------------------------------------------------*/
.sortable-list{
  padding:0;
  margin:0;
  li{
    display:block;
    min-height:206px;
    padding:{
      top:5px;
      bottom:10px;
    }
    border: 1px solid transparent;
    border-right:1px solid #ECECEC;
    //background-color:rgba(255,255,255,.8);
    /*&:nth-child(6n){
      border-right:none;
    }*/
    &:hover{
      box-shadow: 1px 1px 15px 2px rgba(0,0,0,.2);
      .m-waiter-avatar img{
        transform:scale(1.25);
        transition: 1s;
      }

    }
  }
}
.m-waiter-group{
  text-align: center;
  padding:{
    left:10px;
    right:10px;
  }
  p{
    color:$text-content--light;
    margin-bottom:5px;
  }
  .m-waiter-number{
    color:$text-content--dark;
  }
  .m-waiter-states{
    label{
      color:$text-content--light;
      font-weight:400;
    }
  }

}
.m-waiter-avatar{
  display:inline-block;
  &.m-avatar--140{
    max-width:140px;
    max-height:140px;
  }
  &.m-avatar--100{
    max-width:100px;
    max-height:100px;
  }
  img{
    width:100%;
    height:100%;
  }

}
.m-working-icon{
  display:none;
}
.m-element-free{
  .m-waiter-avatar{
    background-color:$color-free;
    border: 3px solid $color-free;
  }
  .m-waiter-states{
    select{
      color:$color-free;
      &:focus{
        border-color:$color-free !important;
      }
    }
  }
}
.m-element-working{
  .m-waiter-avatar{
    background-color:$color-working;
    border: 3px solid $color-working;
  }
  .m-working-icon{
    position: absolute;
    @extend %transform--center;
    border-radius: 50%;
    display:block;
    width:100%;
    height:100%;
    width:calc(100%);
    height:calc(100%);
    background-color:rgba(0,0,0,.3);
    z-index: 99;
    @at-root .m-avatar--100 &{

    }
    i{
      font-size:60px;
      position: absolute;
      @extend %transform--center;
      color:#fff;
    }
  }
  .m-waiter-states{
    select{
      color:$color-working;
      &:focus{
        border-color:$color-working !important;
      }
    }
  }
}
.m-element-rest{
  .m-waiter-avatar{
    background-color:$color-rest;
    border: 3px solid $color-rest;
    img{
      opacity: .5;
    }
  }
  .m-waiter-states{
    select{
      color:$color-rest;
      &:focus{
        border-color:$color-rest !important;
      }
    }
  }
}
.m-waiter-group--sortable{
  .m-waiter-avatar{
    position: relative;
    //padding:4px;
    overflow: hidden;
    margin-bottom:10px;
  }
}
