@import "../mixins/mixins-color";


.m-list-group{
  .m-list_item{
    padding:10px 0;
    border-bottom:1px solid $color-ibox-border;
    &:first-child{
      padding-top:0;
    }
    &:last-child{
      border-bottom:none;
    }
  }
}
.m-list--chart{
  div,span{
    line-height: 30px;
    vertical-align: middle;
  }
  svg{
    vertical-align: middle;
  }
}
.ibox-footer{
  border-top:1px solid #E7EAEC;
  padding:15px 20px 15px;
  background-color:#fff;
}
//iboxoverflow

.m-ibox-overflow-y{
  .m-overflow_inner{
    overflow-y: auto;
    height:400px;
  }
  @at-root{
    &.ibox-content{
      padding-right:5px;
      .m-overflow_inner{
        padding-right:15px;
      }
    }
    &{
      .m-overflow_inner.g-ht--500{
        height:500px;
      }
      .m-overflow_inner.g-ht--300{
        height:300px;
      }
    }
  }
}
/*ibox-title */
.ibox-title_help{
  display:inline-block;
  margin-left:10px;
  line-height: 1.5;
  vertical-align: middle;
  i{
    vertical-align: middle;
  }
}
/*iconfont */
.ibox{
  .iconfont{
    vertical-align: -1px;
  }
}